.custom-select {
    &-container {
        width: 100% !important;
        font-size: 16px !important;
        &.second .custom-select__control {
            min-height: 52px !important;
        }
    }
    &__control {
        width: 100% !important;
        min-width: 150px;
        min-height: 37px;
        background: $lightGrayColor !important;
        border: 2px solid #f8f8f8 !important;
        border-radius: 20px !important;
        padding: 0px 12px !important;
        transition: all 0.3s ease 0s;

        &--menu-is-open {
            background: $lightColor !important;
            border-color: rgba(102, 78, 249, 0.8) !important;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) !important;
        }

        @media (any-hover: hover) {
            &:hover {
                background: $lightColor !important;
                border-color: rgba(102, 78, 249, 0.8) !important;
            }
        }
    }

    &__value-container {
        padding: 0 !important;
        cursor: text !important;
    }

    &__input-container {
        color: $darkColor !important;
    }

    &__indicator-separator {
        display: none !important;
    }

    &__indicators {
        cursor: pointer !important;
    }

    &__indicator {
        padding: 0 !important;
        color: $lightGrayColor2 !important;

        &:hover {
            color: #999aa5 !important;
        }
    }

    &__multi-value {
        background-color: rgba(#999aa5, 0.6) !important;
        border-radius: 0.5rem !important;
    }

    &__multi-value__label {
        font-size: 80% !important;
        color: rgba(white, 0.7) !important;
    }

    &__multi-value__remove {
        color: rgba(white, 0.8) !important;

        &:hover {
            background-color: transparent !important;
            color: white !important;
        }
    }

    &__menu {
        background-color: $lightColor !important;
        color: $darkColor !important;
        animation: scaleIn 0.35s ease-in-out;
        z-index: 1000 !important;
    }

    &__option {
        cursor: pointer !important;

        &--is-active {
            background: $violetColor !important;
        }

        &--is-focused {
            background-color: $lightGrayColor !important;
        }

        &--is-disabled {
            opacity: 0.4 !important;
        }

        &--is-selected {
            background: $violetColor !important;
        }
    }

    &-container {
        > div {
            outline: none !important;
            box-shadow: none !important;
        }

        &:hover,
        &--menu-is-open {
            border-color: #ced4da !important;
        }
    }
}

@keyframes scaleIn {
    0% {
        opacity: 0;
        transform: scale(0.9);
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}
